import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  username: string;
  password: string;
  
  constructor( private authService:AuthService ,
               private _flashMessagesService: FlashMessagesService,
               private router:Router) { }

  ngOnInit() {
  }

  onLoginSubmit(){
    console.log(this.username);

    const user = {
      username: this.username,
      password: this.password
    }

    this.authService.authenticateUser(user).subscribe( (res) => {
      console.log(res);

      if(res.success) {
        this.authService.storeUserData(res.token,res.user);

        this._flashMessagesService.show('You are now logged in' , { cssClass:'alert-success' , timeout : 5000});
        this.router.navigate(['dashboard']);

      }
      else {
        this._flashMessagesService.show(res.msg , { cssClass:'alert-danger' , timeout : 5000});
        this.router.navigate(['login']);
      }

    });

  }

}



