import { Component, OnInit } from '@angular/core';

import { ValidateService } from '../../services/validate.service';

import { AuthService } from '../../services/auth.service';

import { FlashMessagesService } from 'angular2-flash-messages';

import { Router  } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

name:string;
username:string;
email:string;
password:string;


  constructor(private service:ValidateService , 
  private authService:AuthService ,
  private _flashMessagesService: FlashMessagesService,
  private router:Router) { }

  ngOnInit() {
  }

  onRegisterSubmit() {

    this._flashMessagesService.show('We are in about component!', { cssClass: 'alert-danger', timeout: 1000 });
   

    const user = {
      name: this.name,
      email: this.email,
      username: this.username,
      password: this.password
    }

  /*  if(!this.service.validateRegister(user)) {
      return false;
    } */

    this.authService.registerUser(user).subscribe( (data) => {
      if(data.success) {
           this._flashMessagesService.show('You are now registered and can login now.', { cssClass: 'alert-success', timeout: 1000 });
           this.router.navigate(['/login']);
      }
      else {
           this._flashMessagesService.show('You are now registered and can login now.', { cssClass: 'alert-danger', timeout: 1000 });
           this.router.navigate(['/register']);
      }
    });

    
  }

}



